.c-recently-viewed {
  max-width: $s-page-wrap--xlarge;
  display: none;
  margin: 0 auto;

  &.is-visible {
    display: block;
  }

  &__product-thumbnail {
    width: 100%;
    max-width: 25rem;

    &.is-active &-title {
      opacity: 1;
    }
  }

  &__product-thumbnail-title {
    opacity: 0;
    transition: opacity 0.5s;
  }

  &__inner {
    padding: 5.25rem 0 6rem;
    position: relative;
    max-width: 100%;
    overflow: hidden;
    margin: 0 auto;
  }

  &__title {
    text-align: center;
    margin-bottom: 4.1875rem;
  }

  &__carousel {
    display: flex;
  }

  &__nav {
    @include t-absolute-center-y();
    top:45%;
    z-index: 2;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 $s-gutter--edge-mobile;
    pointer-events: none;
  }

  &__nav-arrow {
    padding: .75rem;
    display: block;
    border-radius: 50%;
    pointer-events: all;
    background-color: rgba($s-color--background, .7);
    cursor: pointer;

    .o-icon {
      width: 1rem;
      height: 1rem;
    }
  }

  @media only screen and (min-width: 34.375rem) {
    &__nav {
      padding: 0 5rem;
    }
  }

  @include t-media-query($s-small-up) {
   &.has-2-products{
    .c-recently-viewed__carousel{
       justify-content: center;
       align-items: baseline;
       gap: 2rem;
     }

     .c-recently-viewed__nav{
       display: none;
     }
   }
  }

  @include t-media-query($s-medium-up) {
    position: relative;

    &__carousel{
      max-width: 73.75rem;
      margin: 0 auto;
    }

    &__nav{
      max-width: 95rem;
    }

    &.has-2-products{
      .c-recently-viewed__carousel{
         gap: 7rem;
       }
     }

     &.has-3-products,
     &.has-4-products{
      .c-recently-viewed__carousel{
        justify-content: center;
        align-items: baseline;
        gap: 2rem;
      }
      .c-recently-viewed__nav{
        display: none;
      }
     }

    &__product-thumbnail {
      &-title {
        opacity: 1;
      }
    }

    &__nav {
      padding: 0 3.3%;
      @include t-absolute-center-x-y();
      top:51%;
    }

    &__nav-arrow .o-icon {
      width: 2rem;
      height: 2rem;
    }

    &__inner {
      padding: 7.8125rem 0;
      margin: 0 5.9%;
      position: initial;
    }

    &__title {
      margin-bottom: 1.4375rem;
    }
  }
}